import { AnyObject } from "./types.dto"

export function transformObjKeyToLow(obj: AnyObject) {
    if (obj instanceof Object) {
        const res = {} as AnyObject
        for (const key in obj) {
            const k = humpToLine(key)
            const val = obj[key]
            res[k] = val
        }
        return res
    }
    return obj
}

/**
 * @description: 驼峰转下划线
 * @param {string} str
 * @return {*}
 */
export function humpToLine(str: string) {
    if (!str) {
        return ""
    }
    const first = str.slice(0, 1).toLowerCase()
    const other = str
        .slice(1)
        .replace(/([A-Z])/g, "_$1")
        .toLowerCase()
    return first + other
}

/**
 * @description: 获取uuid
 * @return {*}
 */
export function getUUid() {
    let d = new Date().getTime()
    if (window.performance && typeof window.performance.now === "function") {
        d += performance.now()
    }

    const uuid = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
        /[xy]/g,
        function (c) {
            const r = (d + Math.random() * 16) % 16 | 0
            d = Math.floor(d / 16)
            return (c === "x" ? r : (r & 0x3) | 0x8).toString(16)
        }
    )

    return uuid
}

/**
 * @description: 将uuid转成32位
 * @param {string} uuid
 * @return {*}
 */
export function formatUUidTo32(uuid: string) {
    if (uuid.length > 32) {
        const uuidArr = uuid.split("-")
        return uuidArr.slice(0, 4).join("") + "-" + uuidArr[uuidArr.length - 1]
    }
}

/**
 * @description: 是否为无效值：'', null, undefined, 'null', 'undefined', NaN, 'NaN'
 * @param {any} value
 * @return {*}
 */
export function isInvalidVal(value: any) {
    return ["", null, undefined, "null", "undefined", NaN, "NaN"].includes(
        value
    )
}

/**
 * @description: 将毫秒转为秒
 * @param {number} timestamp
 * @return {*}
 */
export function millisecondToSecond(timestamp: number) {
    return Math.ceil(Number(timestamp) / 1000)
}

/**
 * @description: 获取url参数值
 * @param {string} name
 * @return {string}
 */
export function getQueryString(name: string) {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)")
    var r = window.location.search.slice(1).match(reg)
    if (r != null) return decodeURIComponent(r[2])
    return ""
}

/**
 * @description: 获取cookie
 * @param {string} name
 * @return {*}
 */
export function getCookie(name: string) {
    const strCookie = document.cookie
    const arrCookie = strCookie.split(";")
    for (let i = 0; i < arrCookie.length; i++) {
        let temp = arrCookie[i].split("=")
        if (temp[0].trim() === name) {
            return decodeURIComponent(temp[1])
        }
    }
    return ""
}

/**
 * @description: 设置cookie
 * @param {string} name
 * @param {any} value
 * @param {*} time
 * @return {*}
 */
export function setCookie(name: string, value: any, time = 8 * 60 * 60 * 1000) {
    let str = name + "=" + encodeURIComponent(value)

    if (time > 0) {
        const date = new Date()
        const ms = time
        date.setTime(date.getTime() + ms)
        str += "; expires=" + date.toUTCString()
    }
    str += "; path=/"
    document.cookie = str
}

/**
 * @description: 获取url参数并转为对象
 * @return {*}
 */
export function getUrlParams() {
    var r = window.location.search.substring(1) || ""
    const params = {} as any
    if (r) {
        const paramArr = r.split("&")

        paramArr.length &&
            paramArr.forEach((item) => {
                const temp = item.split("=")
                const key = temp[0]
                const val = temp[1]

                params[key] = val
            })
    }
    return params
}

/**
 * @description: 获取本地存储
 * @param {string} key
 * @return {*}
 */
export function getLocalStorage(key: string) {
    return JSON.parse(localStorage.getItem(key))
}

/**
 * @description: 设置本地存储
 * @param {string} key
 * @param {any} val
 * @return {*}
 */
export function setLocalStorage(key: string, val: any) {
    localStorage.setItem(key, JSON.stringify(val))
}

export function getSessionStorage(key: string) {
    return JSON.parse(sessionStorage.getItem(key))
}

export function setSessionStorage(key: string, val: any) {
    sessionStorage.setItem(key, JSON.stringify(val))
}

export function filterObject(
    obj: AnyObject,
    fun: (key: string, val: any) => {}
) {
    const res = {} as AnyObject
    for (const key in obj) {
        if (fun(key, obj[key])) {
            res[key] = obj[key]
        }
    }
    return res
}

export function ajax(options: AnyObject) {
    let url = options.url
    const method = options.method.toLocaleLowerCase() || "get"
    const async = options.async != false // default is true
    const data = options.data
    const heads = options.heads
    let xhr: AnyObject
    //考虑兼容性
    if (window.XMLHttpRequest) {
        xhr = new XMLHttpRequest()
        // @ts-ignore
    } else if (window.ActiveObject) {
        //兼容IE6以下版本
        // @ts-ignore
        xhr = new ActiveXobject("Microsoft.XMLHTTP")
    }

    if (options.timeout && options.timeout > 0) {
        xhr.timeout = options.timeout
    }
    return new Promise((resolve, reject) => {
        xhr.ontimeout = () => reject && reject("请求超时")

        xhr.onreadystatechange = () => {
            if (xhr.readyState == 4) {
                if (
                    (xhr.status >= 200 && xhr.status < 300) ||
                    xhr.status == 304
                ) {
                    // 接口连接成功时对数据的处理
                    // 先将返回的字符串转换为json格式
                    let result = JSON.parse(xhr.responseText)
                    // 如果接口返回有错误信息error则打印出error（对错误信息做处理，可以是提示，这里用的打印）
                    if (result.error) {
                        console.log(result.error)
                    } else {
                        // 如果接口返回没有错误提示而是返回正确数据时，则调用回调函数，返回正确信息，返回的信息也需要
                        resolve && resolve(result)
                    }
                } else {
                    // 接口连接失败时
                    reject && reject("请求失败")
                    console.log("接口连接失败")
                }
            }
        }
        xhr.onerror = (err: any) => reject && reject(err)
        let paramArr = []
        //如果为GET传输方式的时候 需要将数据拼接到url地址后
        if (method === "get") {
            let encodeData
            if (data instanceof Object) {
                for (let key in data) {
                    paramArr.push(key + "=" + data[key])
                }
                encodeData = paramArr.join("&")
            }
            // 检测 url 中是否已存在 ? 及其位置
            const index = url.indexOf("?")
            if (index === -1) url += "?"
            else if (index !== url.length - 1) url += "&"
            // 拼接 url
            url += encodeData
        }
        xhr.open(method, url, async)
        // 判断传输方式为get还是post ，如果是get则send数据为null,如果为post则将数据转为json字符串传输，其中get的数据已经在上面做了处理
        if (method === "get") xhr.send(null)
        else {
            // post 方式需要设置请求头
            if (heads instanceof Object) {
                for (let key in heads) {
                    xhr.setRequestHeader(key, heads[key])
                }
            }
            xhr.send(JSON.stringify(data))
        }
    })
}
