import { Tracker } from "./tracker"
;(function () {
    const tracker = new Tracker()

    window.tracker = tracker
    // 全局挂载sendTracker
    window.sendTrackEvent = function (
        type: string,
        action: string = "点击",
        label: string,
        callback: Function
    ) {
        tracker.sendTrackEvent(type, action, label, callback)
    }
    // window.addEventListener("load", () => {
    //     bindEvent()
    // })
})()

